import React from 'react';
import calculatedPluginSize from '../utils/calculatedPluginSize';

describe('插件计算方法', () => {
  it('正确计算组件', async () => {
    const testNode = (<span>hello world</span>);
    const result = await calculatedPluginSize(testNode, 644);

    // 正确识别
    expect(result.width).toBe(0);
    expect(result.height).toBe(0);
  });

  it('正确计算文本组件', async () => {
    const testNode = (<>hello world</>);
    const result = await calculatedPluginSize(testNode, 644);

    // 正确识别 text
    expect(result.width).toBe(0);
    expect(result.height).toBe(0);
  });
});
